
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
data() {
return {
activeKey: 1
}
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton
},
beforeCreate(){
this.$store.dispatch('listadress')
},
computed:{
listadress(){
return this.$store.state.listadress
},
},
created(){
document.body.scrollTop = 0
document.documentElement.scrollTop = 0
document.title = '分类'

},
activated(){

},
methods:{
onButtonClick (id, index) {
this.$store.dispatch('defaultById', id)
this.$refs.swipeoutItem[index].close('right')
},
deletes (id, index) {
this.$store.dispatch('deladress', id)
this.$refs.swipeoutItem[index].close('right')
},
details(index){
this.$router.push({path:'/AddAddress',query:{object:this.listadress[index]}})
}
},
}



